export default {
	name: 'matter',
	props: {
		detaildata: Object
	},
	data() {
		return {
			videoing: false,
			staticvideo: ''
		};
	},
	watch: {
		detaildata(newvalue, oldvalue) {
			if (newvalue.videos != '') {
				console.log(newvalue.videos);
				this.staticvideo = newvalue.videos;
				this.videoing = true;
			} else {
				this.videoing = false;
				// console.log('没有视频')
			}
		}
	}
};
